import React from "react"
import Fade from "react-reveal/Fade"
import { Helmet } from "react-helmet"
import { Link } from "gatsby"

import {SRLWrapper} from "simple-react-lightbox";
import SimpleReactLightbox from "simple-react-lightbox";

import {Carousel, CarouselItem} from 'react-bootstrap'
import Nav from "../components/Navbar"
import Banner from "../components/Banner-copy"
import Footer from "../components/Footer"
import Pool from "../assets/statera-banner.png"
import Scoreboard from "../assets/statera-scoreboard.png"
import JourneyMapping from "../assets/statera-journeymapping.png"
import SwimmerJourneyMap from "../assets/statera-parents-perspective.png"
import CoachJourneyMap from "../assets/statera-coach-perspective.png"
import SwimmerJourneyMapSmall from "../assets/statera-parents-perspective-small.png"
import CoachJourneyMapSmall from "../assets/statera-coach-perspective-small.png"
import Responsive from "../assets/statera-responsive.png"
import StressAssessment from "../assets/stressassessment.webm"
import StressAssessmentBackup from "../assets/stress-assessment.mp4"
import StressAssessmentGif from "../assets/stress-assessment.gif"

import favicon from "../assets/favicon.png"
const options ={
  settings: {
    autoplaySpeed: 0,
  },
  buttons: {
    showCloseButton: true,
  }
}

export default ()=> (

  <div id="outercontainer">
  <Helmet>
    <meta charSet="utf-8" name="viewport" content="width=device-width, initial-scale=1.0" />
    <title>Michael Silvestre</title>
    <link rel="canonical" href="http://msilvestre.xyz" />
    <link rel="icon" href={favicon} />
  </Helmet>
  <Nav />
  <Fade duration={3000}>
  <Banner
    image={Pool}
    title="Statera"
    color="#727F84"
    description="Helping student athletes communicate stress to coaches and reduce burnout"
    team="Team: Amrita Sakhrani, Kathy Song"
    role="Role: User Research, Responsive Website, Mobile Design"
    projectLength="Timeline: August 2019 - December 2019"
  />
  </Fade>
  <div className="content">
  <div className="content-container">
  <div className="center-content">
  <div className="section-header"> THE CLIFF'S NOTES</div><br/><br/>
  <strong>Problem</strong>
  <div className="serif-header">High School swimmers struggle balancing school, practice, and meets, leading talented athletes to prematurely quit their teams due to stress and burnout.</div><br/><br/>

  <strong>Solution</strong><br/>
  <div className="serif-header">Give coaches visibility into the hectic high school schedule.</div>
  Statera is a platform for mobile and desktop that establishes a dialogue between student athletes and coaches around emotional health and sports to help decrease burnout. Through Statera, students can assess and submit their current stress levels to their coaches to build practice regimens considerate of their students academic workloads.
  </div>
  <div className="img-text-box blue-grey">
    <div><img src={Responsive} alt="responsive website"/></div>
    <div className="img-text-text"><strong>Building Responsiveness</strong><br/>
  We chose to create a responsive website that would allow coaches and students to allocate energy that felt appropriate to their environment. When coaches prepare for practices, this usually involves carefully reviewing past heat sheets, student records, and building off this data, which means this work typically isnt completed on a mobile phone. However, we wanted to give coaches the opportunity to review any information around students that they might need refreshing while at the pool.</div>
  </div>
  <div className="img-text-box">
  <div className="img-text-text"><strong>Stress Assessment</strong><br/>
  Initial feedback we got from our stress assessment was that it failed to account for students uncomfortable with disclosing personal information, as well as student who wouldnt have a framework for assesing their own stress levels. Incorporating this feedback, we generated a survey that students could use to build into this. </div>
    <div><img src={StressAssessmentGif} alt="preview of stress assessment" /></div>
  </div>
  <div className="center-content">
  <div className="section-header">AND NOW FOR SOME CONTEXT</div><br/><br/>
    <strong>About the project:  </strong>
  My team was asked to build a responsive website for a problem within youth sports as part of a six week project in our Interaction Design Studio. It was our task to go into the field and identify pain points, generate solutions, and create a mid-to-hi-fi prototype of our website.<br/><br/>

    <strong>My role:  </strong><br/>
    <strong>Guerrilla and background research   </strong>- I took to the streets of Pittsburgh and asked people their experiences around youth sports.<br/><br/>

    <strong>Research synthesis   </strong>- Clustered research findings and transformed them into personas and customer journey maps.<br/><br/>

    <strong>Ideation  </strong> - generated storyboards and speed dated those ideas to gauge their validity.<br/><br/>

    <strong>Solutioning  </strong> - Created wireframes and a prototype for our end product.

  </div>

  <div className="image-box blue-grey">
  <Fade duration={3000}>
  <SimpleReactLightbox>
  <SRLWrapper>
  <a href={Scoreboard} data-attribute="SRL">
    <img src={Scoreboard} alt="Scoreboard at Gateway High School pool"/>
    </a>
    <a href={JourneyMapping} data-attribute="SRL">
    <img src={JourneyMapping} alt="My team plotting out our user journey maps"/>
    </a>
  </SRLWrapper>
  </SimpleReactLightbox>

    </Fade>
  </div>
  <div className="center-content">
  <div className="section-header"> DISCOVERY AND UNDERSTANDING</div><br/><br/>
  <strong>  Finding the right problem to solve:</strong><br/>
  Our team performed guerrilla research and interviewed residents within Pittsburgh on their experiences around youth sports. We spoke to volunteer coaches, former student athletes, and parents during our research process. Across these groups, we uncovered a similar story in the student athlete experience: while athletes didnt lose their passion towards sports, they felt too much pressure in balancing academics and athletics to continue pursuing it during their education.
  </div>
  <div className="quote-box blue-grey"><Fade duration={3000}><div className="quote">"While my son had to stop swimming on a team so he could focus on his classes, he never stopped loving to swim. He continues to find time every week to head to the pool."
  -Mom of two swimmers</div></Fade>
  </div>
  <div className="center-content">
As swimming happened to be the seasonal sport currently in progress in Pittsburgh, we chose to focus our research on the young swimmers experience. On our weekend, we hitched an Uber to the Pittsburgh suburbs to observe a Monroeville Marlins swim meet. It was early winter, but we were welcomed with a wave of humid air immediately after stepping into that gym.</div>
<div className="center-content">
<div className="section-header"> FINDINGS</div><br/><br/>
As our team performed interviews and observations, wed come together to discuss what we did know and what gaps we had in our knowledge about the swim team experience. Through these conversations, we created journey maps that highlighted how coaches and swimmers (along with their parents) navigated through a week of practice and competitions.<br/><br/>

Ultimately, we highlighted three critical insights that were important for what we chose to build a website solving:<br/><br/>
<div className="insightSection">
<div className="insight-group">
<div className="insightNumber">01.</div>
<div className="insight">Swim coaches spend their time during meets standing right by the edge of the pool. They are busy focusing on their students. Using a website during a meet would not only be dangerous, it would also distract them from their role as a coach.</div></div>
<div className="insight-group">
<div className="insightNumber">02.</div>
<div className="insight">The downtime during swim meets offers a lot of opportunity for swimmers to do school work, but the loudness of the gym and wanting to show support for your team can take away from that.</div>
</div>
<div className="insight-group">

<div className="insightNumber">03.</div>
<div className="insight">Swim meets generate a lot of useful data for coaches through heat sheets. This information is constantly being updated through the meet but could be giving coaches more value.</div></div>
</div>
</div>
<div className="image-box blue-grey">
<Fade duration={3000}>
<SimpleReactLightbox>
<SRLWrapper>
<a href={SwimmerJourneyMap} data-attribute="SRL">
  <img src={SwimmerJourneyMapSmall} alt="Scoreboard at Gateway High School pool"/>
  </a>
  <a href={CoachJourneyMap} data-attribute="SRL">
  <img src={CoachJourneyMapSmall} alt="My team plotting out our user journey maps"/>
  </a>
</SRLWrapper>
</SimpleReactLightbox>

  </Fade>
</div>
<div className="center-content">
<div className="section-header"> CONCEPT VALIDATION</div><br/><br/>
After discussing our research as a team, we identified four main opportunity areas for building a website around. These included:<br/><br/>

<ol>
<li>How might we avoid overwhelming the coach with too many questions and trying to interpret multiple heat sheets?</li>
<li>How might we effectively communicate with swimmers about matters that need attention during the meet?</li>
<li>How might we keep our athletes well balanced to avoid them from getting overworked and burning out?</li>
<li>How might we get there on time for my kid's event without having to stay the entire time?</li>
<li>How might we prepare the correct warm-up, advice and medley choice for swimmers before day two of the meet?</li>
</ol><br/><br/>

We then mocked up storyboards around these opportunity statements, and speed dated them with parents and former swim athletes to get their feedback. From these, it seemed like the most extreme response came from addressing student burnout. Though it felt like the biggest challenge, our team was excited at tackling a space that could lead to more meaningful change within the current state of student-coach relationship dynamics.
</div>


  </div>
  </div>
<Footer />
</div>
)
